.webaudio-starter .options-output {
  margin: 1em 0;
}
.webaudio-starter .options-output .option {
  margin: 0.3em 0 0.3em 15em;
}
.webaudio-starter .options-output .option.void {
  margin-left: 1em;
}
.webaudio-starter .options-output .option label:first-child {
  margin-left: -15em;
  display: inline-block;
  width: 15em;
  text-align: right;
}
.webaudio-starter .options-output .option .min {
  display: inline-block;
  width: 4em;
  text-align: right;
}
.webaudio-starter .options-output .option .max {
  display: inline-block;
  width: 4em;
  text-align: left;
}
.webaudio-starter .options-output .option input[type="number"] {
  width: 4.5em;
}
.webaudio-starter .options-output .option input[type="text"] {
  width: 30em;
}
.webaudio-starter .options-output .option input:invalid {
  background: #F8A;
}
.webaudio-starter .options-output .draggable-with-handle {
  position: relative;
  margin: 0.3em 1em;
}
.webaudio-starter .options-output .draggable-with-handle.ghost {
  opacity: .4;
}
.webaudio-starter .options-output .draggable-with-handle .option {
  margin-top: 0;
  margin-bottom: 0;
}
.webaudio-starter .options-output .draggable-with-handle [draggable],
.webaudio-starter .options-output .draggable-with-handle .delete {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 1em;
}
.webaudio-starter .options-output .draggable-with-handle [draggable] {
  left: -1em;
  cursor: move;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAAJCAYAAADgkQYQAAAAGUlEQVQYlWNgIAYYGxv/J4SJMog4MFStAwDfUCsJGefs4gAAAABJRU5ErkJggg==) center no-repeat, url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAAJCAYAAADgkQYQAAAAF0lEQVQYlWNgIBZ0dHT8J8SmIhiK1gEAJMkXUFw51ewAAAAASUVORK5CYII=) center;
}
.webaudio-starter .options-output .draggable-with-handle .delete {
  right: -1em;
  cursor: default;
  border: dotted 1px #888;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAAJCAYAAADgkQYQAAAAJklEQVQYlWNgIBYYGxv/J0ocXQCXRrgETgUETSDaJIJuItp3+AAAbDIUubYyZeYAAAAASUVORK5CYII=) center no-repeat;
}
.webaudio-starter .code-output {
  margin: 1em 0;
}
.webaudio-starter .code-output .code pre {
  border: solid 1px #000;
}
.webaudio-starter .code-output .code p {
  border: solid 1px #444;
  color: #444;
  padding: 0.5em;
}
.webaudio-starter .code-output details {
  display: block;
  margin: 1em 0;
}
.webaudio-starter .tip-warn {
  display: inline-block;
  position: relative;
  width: 1.3em;
  height: 1.3em;
  border: solid 1px black;
  border-radius: 0.65em;
  background: #FF8;
  color: #333;
  font-family: sans-serif;
  font-size: 0.8em;
}
.webaudio-starter .tip-warn .tip-content {
  display: none;
}
.webaudio-starter .tip-warn .tip-content kbd {
  font-size: 1.25em;
}
.webaudio-starter .tip-warn:hover .tip-content {
  display: block;
  position: absolute;
  top: -1px;
  left: -1px;
  padding: 0.65em;
  border: solid 1px black;
  border-radius: 0.65em;
  background: #FF8;
  width: 20em;
}
.webaudio-starter .tip-warn::before {
  display: inline-block;
  content: '!';
  width: 1.3em;
  height: 1.3em;
  text-align: center;
}
